import React from 'react';

const Card=({name,email,id})=>{
	return(
		<div className='bg-dark-gray dib br3 pa3 ma2 grow shadow-5'>
	    <img alt= 'Ankit Mishra' width="200" height="200" src={`https://robohash.org/${id}?size=200x200`}/>
		<div> 
			<h2>{name}</h2>
			<p>{email}</p>
		</div>			
		</div>
		);
}

export default Card;